import React, { useState } from 'react'
import { Link } from 'gatsby'
import './Navbar.css'

import { HiMenuAlt1 } from 'react-icons/hi'

function Navbar() {
    const [toggle, setToggle] = useState(false);

    const toggleState = () => {
        setToggle(!toggle)
    }

    return (
        <nav>
            <h4><Link className="brand" to="/">Pioyi's Blog</Link></h4>
            <ul className={toggle ? "nav-links" : "nav-links hidden"}>
                <Link to="/" activeClassName="link--active">Home</Link>
                <Link to="/blog/how-it-was-made" activeClassName="link--active">How it was Made</Link>
            </ul>
            <HiMenuAlt1 
                className={toggle ? "nav-menu active" : "nav-menu"}
                onClick={toggleState}
            />  
        </nav>
    )
}

export default Navbar
